import React, { useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { useParams } from 'react-router';
import { setActivePageNo } from './Actions';
import Article from './Article';
import { ArticleDataType, PropName } from './Constant';
import PageNavigator from './PageNavigator';
import { StoreStateType } from './Reducer';
import Utils from './Utils';

interface RouterParamType {
  [PropName.ACTIVE_PAGE_NO]: string;
}

const NewArticles: React.FC = () => {
  const activePageNo = parseInt(useParams<RouterParamType>()[PropName.ACTIVE_PAGE_NO]);
  const dispatch = useDispatch();

  useEffect(() => {
    Utils.scrollToTop();
    Utils.setTitle(`zoo zoo town - page ${activePageNo}`);
    Utils.sendAccessLog(window.location.pathname + window.location.hash);
    dispatch(setActivePageNo(activePageNo));
  }, [activePageNo, dispatch]
  );

  const activeDataList: ArticleDataType[] =
    useSelector<StoreStateType, ArticleDataType[]>(
      state => state[PropName.ACTIVE_ARTICLE_DATA_LIST]
    );

  useEffect(Utils.loadTwitter, [activeDataList]);

  return (
    <>
      <div className="d-flex flex-column align-items-center" >
        {activeDataList.map((articleData, index) =>
          <Article articleData={articleData} key={index} />
        )}
      </div>
      <PageNavigator />
    </>
  );
};

export default NewArticles;
