import React, { useEffect } from 'react';
import { useDispatch } from 'react-redux';
import { Redirect, Route, Switch } from 'react-router-dom';
import { setArticleDataList } from './Actions';
import { ArticleDataType, ARTICLE_DATA_URL, PropName } from './Constant';
import Header from "./Header";
import MainWidthStyle from './MainWidthStyle';
import Menu from './Menu';
import NewArticles from './NewArticles';

const App: React.FC = () => {
  const dispatch = useDispatch();
  useEffect(() => {
    console.log('fetch');
    fetch(ARTICLE_DATA_URL)
      .then(response => response.json())
      .then(json => {
        const articleDataList: ArticleDataType[] = [];
        for (const item of json.feed.entry) {
          const articleData: ArticleDataType = {
            [PropName.EMBEDCODE]: item[`gsx$${[PropName.EMBEDCODE]}`].$t
          };
          articleDataList.push(articleData);
        }
        articleDataList.reverse();// 新しいもの順に並べ替える
        dispatch(setArticleDataList(articleDataList));
      });
  }, [dispatch]);

  return (
    <>
      <MainWidthStyle>
        <Header />
      </MainWidthStyle>

      <section id="main">
        <MainWidthStyle>
          <Switch>
            <Route exact path={`/menu`} component={Menu} />
            <Route exact path={`/:${PropName.ACTIVE_PAGE_NO}`} component={NewArticles} />

            // 上記のパスに一致しないものは1ページ目にとばす
            <Route render={
              (): JSX.Element => <Redirect to="/1" />
            } />
          </Switch>
        </MainWidthStyle>
      </section>
    </>
  );
};

export default App;