import React from 'react';
import { Link } from 'react-router-dom';
import { StoreStateType } from './Reducer';
import { useSelector, useDispatch } from 'react-redux';
import { PropName } from './Constant';
import { setArticlesPerPage } from './Actions';

const Menu: React.FC = () => {
  const dispatch = useDispatch();
  const articlesPerPage: number =
    useSelector<StoreStateType, number>(
      state => state[PropName.ARTICLES_PER_PAGE]
    );

  const handleChange = (e: React.ChangeEvent<HTMLInputElement>): void => {
    switch (e.target.name) {
      case PropName.ARTICLES_PER_PAGE: {
        dispatch(setArticlesPerPage(parseInt(e.target.value)));
      }
    }
  };

  return (
    <div className="d-flex flex-column">
      <div className="card m-2">
        <div className="card-header">
          記事表示数
        </div>
        <div className="card-body mx-auto">
          <input type="number" value={articlesPerPage}
            onChange={handleChange} className="form-control"
            name={PropName.ARTICLES_PER_PAGE} id={PropName.ARTICLES_PER_PAGE} />
        </div>
      </div>

      <div className="card m-2">
        <div className="card-header">
          このサイトを紹介する
        </div>
        <div className="card-body mx-auto">
          <img src="qr.png" alt="qrcode" />
        </div>
      </div>

      <div className="mx-auto">
        <Link to="/1" >
          <img src="navigationj_home.png" alt="top" />
        </Link>
      </div>
    </div>
  );
};

export default Menu;