import { ActionObjectType, ActionType, PayloadType } from "./Actions";
import { ArticleDataType, HTTPS_YOUTUBE_URL, HTTP_YOUTUBE_URL, PropName } from "./Constant";
import { PageManager, PageManagerFactory } from "./PageManager";

export interface StoreStateType {
    [PropName.ACTIVE_PAGE_NO]: number;
    [PropName.PREVIOUS_PAGE_NO]: number;
    [PropName.NEXT_PAGE_NO]: number;
    [PropName.ARTICLES_PER_PAGE]: number;
    [PropName.ARTICLE_DATA_LIST]: ArticleDataType[];
    [PropName.ACTIVE_ARTICLE_DATA_LIST]: ArticleDataType[];
}

const INITIAL_STATE: StoreStateType = {
    [PropName.ACTIVE_PAGE_NO]: 1,
    [PropName.PREVIOUS_PAGE_NO]: 1,
    [PropName.NEXT_PAGE_NO]: 1,
    [PropName.ARTICLES_PER_PAGE]: 5,
    [PropName.ARTICLE_DATA_LIST]: [],
    [PropName.ACTIVE_ARTICLE_DATA_LIST]: []
};

export default function Reducer(state = INITIAL_STATE, action: ActionObjectType): StoreStateType {
    switch (action.type) {
        case ActionType.UPDATE_DATA: {
            return new StateBuilder(state, action.payload).build();
        }

        default: {
            return state;
        }
    }
}

class StateBuilder {
    private state: StoreStateType;
    private changedPropNames: Set<string>;

    constructor(state: StoreStateType, payload: PayloadType) {
        this.state = { ...state, ...payload };

        this.changedPropNames = new Set(Object.keys(payload));
    }

    build(): StoreStateType {
        const articleDataList: ArticleDataType[] = this.state[PropName.ARTICLE_DATA_LIST]
            .filter(removeEmptyDataFilter);

        const pageManager: PageManager =
            PageManagerFactory.newDefaultPageManager(
                articleDataList.length, this.state[PropName.ARTICLES_PER_PAGE], this.state[PropName.ACTIVE_PAGE_NO]);

        // fetchされる前はarticleDataListが空なのでpageManagerを使用するとページが0になる
        // this.state[PropName.ACTIVE_PAGE_NO] = pageManager.getActivePageNo();

        this.state[PropName.PREVIOUS_PAGE_NO] = pageManager.getPreviousPageNo();
        this.state[PropName.NEXT_PAGE_NO] = pageManager.getNextPageNo();
        this.state[PropName.ARTICLES_PER_PAGE] = pageManager.getArticlesPerPage();
        this.state[PropName.ARTICLE_DATA_LIST] = articleDataList;
        this.state[PropName.ACTIVE_ARTICLE_DATA_LIST] = articleDataList.slice(
            pageManager.getActivePageFirstDataIndex(),
            pageManager.getActivePageLastDataIndex())
            .map(httpToHttpsMapper);

        return this.state;
    }
}

function removeEmptyDataFilter(articleData: ArticleDataType): boolean {
    return articleData[PropName.EMBEDCODE] !== '';
}

// 埋め込みコード内のURLがhttpになっているのでエラー防止のためにhttpsに置換する
function httpToHttpsMapper(articleData: ArticleDataType): ArticleDataType {
    let newArticleData: ArticleDataType = { ...articleData }; //deep copy
    newArticleData[PropName.EMBEDCODE] =
        newArticleData[PropName.EMBEDCODE].replace(HTTP_YOUTUBE_URL, HTTPS_YOUTUBE_URL);
    return newArticleData;
}