export default class Utils {
    public static scrollToTop(): void {
        window.scroll(0, 0);
    }

    public static scrollToElement(id: string): void {
        const element: HTMLElement | null = document.getElementById(id);
        if (element != null) {
            element.scrollIntoView(true);
        }
    }

    public static setTitle(title: string): void {
        document.title = title;
    }

    public static sendAccessLog(path: string): void {
        // for Google Analytics
        // index.htmlで定義している関数
        // HACK
        try {
            (window as any).sendAccessLog(path);
        } catch (error) {
            console.log('sendAccessLogError');
        }
    }

    public static loadTwitter(): void {
        // for twitter
        // index.htmlで定義している関数
        // HACK
        try {
            (window as any).twttr.widgets.load();
        } catch (error) {
            console.log('loadTwitterError');
        }
    }
}