import React from 'react';
import { useSelector } from 'react-redux';
import { Link } from "react-router-dom";
import { PropName } from './Constant';
import { StoreStateType } from './Reducer';

const PageNavigator: React.FC = () => {
  const previousPageNo: number =
    useSelector<StoreStateType, number>(state => state[PropName.PREVIOUS_PAGE_NO]);

  const nextPageNo: number =
    useSelector<StoreStateType, number>(state => state[PropName.NEXT_PAGE_NO]);

  return (
    <>
      <div className="mx-auto fixed-bottom p-2 d-flex justify-content-around"
        style={{ width: 'inherit' }}>
        <div>
          <Link to={`${previousPageNo}`} >
            <img src="navigationj_prev.png" alt="prev" />
          </Link>
        </div>

        <div className="d-flex flex-column justify-content-end">
          <Link to={`/menu`}>
            <img className="border border-secondary rounded" src="justify.svg" alt="" width="72" height="72" />
          </Link>
        </div >

        <div>
          <Link to={`${nextPageNo}`}>
            <img src="navigationj_next.png" alt="next" />
          </Link>
        </div>
      </div>

      <div style={{ height: "112px" }}>
        {
          //float margin
        }
      </div>
    </>
  );
};

export default PageNavigator;