import React from 'react';
import { ArticleDataType, PropName } from './Constant';

interface PropType {
  [PropName.ARTICLE_DATA]: ArticleDataType;
}

const Article: React.FC<PropType> = (props: PropType) => {
  const embedcode: string = props[PropName.ARTICLE_DATA][PropName.EMBEDCODE];

  return (
    <div className="card m-2">
      <div className="card-body">
        <div dangerouslySetInnerHTML={{
          __html: embedcode
        }}>
        </div>
      </div>
    </div>
  );
};

export default Article;