const FIRST_PAGE_NO: number = 1;

export interface PageManager {
    getPreviousPageNo(): number;
    getActivePageNo(): number;
    getNextPageNo(): number;
    getLastPageNo(): number;
    getArticlesPerPage(): number;
    getActivePageFirstDataIndex(): number;
    getActivePageLastDataIndex(): number;
}

export class PageManagerFactory {
    static newDefaultPageManager(articleDataListSize: number,
        articlesPerPage: number,
        activePageNo: number): PageManager {
        return PageManagerFactory.newLoopPageManager(articleDataListSize,
            articlesPerPage,
            activePageNo);
    }

    static newNormalPageManager(articleDataListSize: number,
        articlesPerPage: number,
        activePageNo: number): PageManager {
        return new NormalPageManager(articleDataListSize,
            articlesPerPage,
            activePageNo);
    }

    static newLoopPageManager(articleDataListSize: number,
        articlesPerPage: number,
        activePageNo: number): PageManager {
        return new LoopPageManager(articleDataListSize,
            articlesPerPage,
            activePageNo);
    }
}

class NormalPageManager implements PageManager {
    constructor(private articleDataListSize: number,
        private articlesPerPage: number,
        private activePageNo: number) {
    }

    protected correctPageNo(pageNo: number): number {
        const lastPageNo: number = this.getLastPageNo();

        if (pageNo < FIRST_PAGE_NO) {// 下限
            return FIRST_PAGE_NO;
        } else if (lastPageNo < pageNo) {// 上限
            return lastPageNo;
        } else {// 正常値
            return pageNo;
        }
    }

    getPreviousPageNo(): number {
        return this.correctPageNo(this.activePageNo - 1);
    }

    getActivePageNo(): number {
        return this.correctPageNo(this.activePageNo);
    }

    getNextPageNo(): number {
        return this.correctPageNo(this.activePageNo + 1);
    }

    getLastPageNo(): number {
        return Math.ceil(this.articleDataListSize / this.getArticlesPerPage());
    }

    getArticlesPerPage(): number {
        const articlesPerPage: number = this.articlesPerPage;

        if (articlesPerPage < 1) {// 下限
            return 1;
        } else if (20 < articlesPerPage) {// 上限
            return 20;
        } else {// 正常値
            return articlesPerPage;
        }
    }

    getActivePageFirstDataIndex(): number {
        return (this.getActivePageNo() - 1) * this.getArticlesPerPage();
    }

    getActivePageLastDataIndex(): number {
        return this.getActivePageFirstDataIndex() + this.getArticlesPerPage();
    }
}

class LoopPageManager extends NormalPageManager {
    protected correctPageNo(pageNo: number): number {
        const lastPageNo: number = this.getLastPageNo();

        if (pageNo < FIRST_PAGE_NO) {// 下限
            return (pageNo % lastPageNo) + lastPageNo;
        } else if (lastPageNo < pageNo) {// 上限
            return pageNo % lastPageNo;
        } else {// 正常値
            return pageNo;
        }
    }
}