import React from 'react';
import ReactDOM from 'react-dom';
import { Provider } from "react-redux";
import { HashRouter as Router } from 'react-router-dom';
import { createStore } from 'redux';
import { persistReducer, persistStore } from 'redux-persist';
import { PersistGate } from 'redux-persist/integration/react';
import storage from 'redux-persist/lib/storage';
import App from './App';
import { PropName } from './Constant';
import './index.css';
import Reducer from './Reducer';
import * as serviceWorker from './serviceWorker';

const persistConfig = {
    key: 'root',
    storage,
    whitelist: [PropName.ARTICLES_PER_PAGE],
};

const persistedReducer = persistReducer(persistConfig, Reducer);

// HACK
const store = createStore(persistedReducer, (window as any).__REDUX_DEVTOOLS_EXTENSION__ && (window as any).__REDUX_DEVTOOLS_EXTENSION__());
const pstore = persistStore(store as any);

ReactDOM.render(
    <Provider store={store}>
        <PersistGate loading={<p>loading...</p>} persistor={pstore}>
            <Router>
                <App />
            </Router >
        </PersistGate>
    </Provider>,
    document.getElementById('root'));
serviceWorker.unregister();