import { Action } from "redux";
import { ArticleDataType, PropName } from "./Constant";
import { StoreStateType } from "./Reducer";

export enum ActionType {
    UPDATE_DATA
}

export type PayloadType = Partial<StoreStateType>;

export interface ActionObjectType extends Action {
    type: ActionType;
    payload: PayloadType;
}

export function setArticlesPerPage(articlesPerPage: number): ActionObjectType {
    return {
        type: ActionType.UPDATE_DATA,
        payload: {
            [PropName.ARTICLES_PER_PAGE]: articlesPerPage
        }
    };
}


export function setActivePageNo(activePageNo: number): ActionObjectType {
    return {
        type: ActionType.UPDATE_DATA,
        payload: {
            [PropName.ACTIVE_PAGE_NO]: activePageNo
        }
    };
}

export function setArticleDataList(articleDataList: ArticleDataType[]):
    ActionObjectType {
    return {
        type: ActionType.UPDATE_DATA,
        payload: {
            [PropName.ARTICLE_DATA_LIST]: articleDataList
        }
    };
}
