// propsとstateで利用するプロパティ名
export enum PropName {
    ACTIVE_PAGE_NO = 'activePageNo',
    PREVIOUS_PAGE_NO = 'previousPageNo',
    NEXT_PAGE_NO = 'nextPageNo',

    ARTICLES_PER_PAGE = 'articlesPerPage',
    
    ARTICLE_DATA = 'articleData',
    ARTICLE_DATA_LIST = 'articleDataList',
    ACTIVE_ARTICLE_DATA_LIST = 'activeArticleDataList',

    EMBEDCODE = 'embedcode',// データベースの列名(googleの仕様上アンダーバーが消える)
}

export interface ArticleDataType {
    [PropName.EMBEDCODE]: string;
}

export const SPREADSHEET_ID: string =
    '1jDoKicjNhz0ZOJRGP0K3Dw-4ioqcc1xsLyOYz0cM_rU';

export const ARTICLE_DATA_URL: string = getSpreadsheetURL(SPREADSHEET_ID);

function getSpreadsheetURL(spreadsheetID: string): string {
    return `https://spreadsheets.google.com/feeds/list/${spreadsheetID}/od6/public/values?alt=json`;
}

export const HTTP_YOUTUBE_URL: string = 'http://youtube.com';
export const HTTPS_YOUTUBE_URL: string = 'https://youtube.com';